import React, { Component } from "react";
import { Snackbar, Box, List, ListItem, Fab } from "@mui/material";
import AddIcon from "@mui/icons-material/Add";
import DialogTitle from "@mui/material/DialogTitle";
import Dialog from "@mui/material/Dialog";
import NewPost from "../NewPost/NewPost";
import * as consts from "../../util/const";
import Post from "../Post/Post";

class Dashboard extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      open: true,
      newPost: false,
      user: Object(),
      posts: [],
      snackbarMessage: "Welcome to Easy volunter",
    };
    this.onclick = this.onclick.bind(this);
    this.onClose = this.onClose.bind(this);
    this.open = this.open.bind(this);
    this.close = this.close.bind(this);
    this.postCreated = this.postCreated.bind(this);
    this.getPost = this.getPost.bind(this);
  }

  componentDidMount() {
    this.getUser();
    this.getPost();
    console.log("State " + this.state.users);
  }

  getUser() {
    console.log("userId :" + sessionStorage.getItem("id"));
    fetch(`${consts.svr}` + "/profile/" + sessionStorage.getItem("id"), {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => res.json())
      .then((user) => {
        let profile = user.collection;
        console.log("User " + profile);
        this.setState({
          user: profile,
        });
      });
  }

  getPost() {
    fetch(`${consts.svr}` + "/post/get/", {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    })
      .then((res) => res.json())
      .then((posts) => {
        let post = posts.query;
        this.setState({
          ...this.state,
          posts: post,
        });
      });
  }

  onclick() {
    this.setState({ ...this.state, open: true });
  }

  onClose(event, reason) {
    if (reason === "clickaway") {
      return;
    }
    this.setState({ ...this.state, open: false });
  }

  open() {
    console.log(this.state);
    this.setState({ ...this.state, newPost: true });
  }

  close() {
    this.setState({ ...this.state, newPost: false });
  }

  postCreated(posted) {
    if (posted.post) {
      this.setState({
        ...this.state,
        posts: posted.posts,
        newPost: false,
        open: true,
        snackbarMessage: "You created a post",
      });
    } else {
      this.setState({
        ...this.state,
        newPost: false,
        open: true,
        snackbarMessage: "Something wrong happened, please try again",
      });
    }
  }
  render() {
    // this.onclick();
    const fabStyle = {
      position: "absolute",
      bottom: 16,
      right: 16,
      backgroundColor: "#f15a24",
    };

    return (
      <div>
        <h1>Dashboard</h1>
        <Snackbar
          open={this.state.open}
          autoHideDuration={6000}
          onClose={this.onClose}
          message={this.state.snackbarMessage}
        />
        <div className="d-flex justify-content-center pt-5wf4">
          <Box fullWidth sx={{ maxWidth: "lg", width: 600, padding: 4 }}>
            <List>
              {this.state.posts.map((post) => (
                <ListItem
                  sx={{
                    borderRadius: 1,
                    border: 1,
                    borderColor: "blue",
                    mx: "auto",
                    margin: 2,
                  }}
                >
                  <Post post={post} />
                </ListItem>
              ))}
            </List>
          </Box>
        </div>

        <Box sx={{ "& > :not(style)": { m: 1 } }}>
          <Fab sx={fabStyle} aria-label="add" onClick={this.open}>
            <AddIcon />
          </Fab>
        </Box>

        <div>
          <Dialog fullWidth onClose={this.close} open={this.state.newPost}>
            <DialogTitle>New Post</DialogTitle>
            <NewPost
              user={this.state.user}
              onClose={this.close}
              postCreated={this.postCreated}
            />
          </Dialog>
        </div>
      </div>
    );
  }
}

export default Dashboard;
