import React, { Component } from "react";
import { Box, Grid, Button } from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";

class Post extends Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      post: this.props.post,
    };
  }
  render() {
    const iconStyle = {
      position: "absolute",
      top: 16,
      right: 16,
      cursor: "pointer",
      fontSize: 20,
    };
    console.log("Profiles");
    console.log(this.state.post.profile.length > 0);
    return (
      <div className="w-100">
        <Box fullWidth sx={{ with: "100%" }}>
          <MoreVertIcon sx={iconStyle} />
          <Grid container spacing={2}>
            <Grid item sx={{}}>
              <AccountCircleIcon sx={{ fontSize: 40, paddingRight: 1 }} />
            </Grid>
            <Grid item>
              <Box sx={{}}>
                {this.state.post.profile.length > 0
                  ? this.state.post.profile[0].name +
                    " " +
                    this.state.post.profile[0].lastName
                  : "No name"}
              </Box>
              <Box sx={{ fontSize: 10 }}>
                {new Date(this.state.post.createdDate).toLocaleDateString(
                  "en-US",
                  {
                    weekday: "short",
                    year: "numeric",
                    month: "long",
                    day: "numeric",
                  }
                )}
              </Box>
            </Grid>
          </Grid>
          <Box fullWidth>{this.state.post.message}</Box>
          <Box fullWidth>
            <Button
              sx={{
                backgroundColor: "#22b573",
                color: "#ffffff",
                float: "right",
              }}
            >
              Chat
              {this.state.post.profile.length > 0
                ? " with " + this.state.post.profile[0].name
                : ""}
            </Button>
          </Box>
        </Box>
      </div>
    );
  }
}

export default Post;
